<template>
	<div id="setDescLabel">
		<similarHeader title='设置备注和标签' @goBack='goBack' :haveMore='false'>
			<span :class="['btn',{'color':isChangeColor}]" >
				完成
			</span>
		</similarHeader>
		<div class="main">
			<span class='title'>备注名</span>
			<van-cell-group>
			  <van-field v-model="remarkName" clearable @input='changeName'  />
			</van-cell-group>
			<span class='title'>标签</span>
			<van-cell-group>
			  <van-field v-model="label" clearable @input='changeValue'  placeholder="添加标签对联系人进行分类"/>
			</van-cell-group>
			<span class='title'>电话号码</span>
			<van-cell-group>
			  <van-field v-model="phone" clearable @input='changeValue'  placeholder="添加电话号码"/>
			</van-cell-group>
			<span class='title'>描述</span>
			<van-cell-group>
			  <van-field
			    v-model="desc"
			    rows="1"
			    autosize
			    type="textarea"
			    class='paddingBottom'
			    @input='changeValue'
			    placeholder="添加更多备注信息"
			  />
			  <van-uploader :after-read="afterRead" upload-text='添加名片或相关图片'/>
			</van-cell-group>
		</div>
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{
				remarkName:'',
				label:'',
				phone:'',
				desc:'',
				isChangeColor:false
			}
		},
		props:['nowChatNickname'],
		methods:{
			goBack(){
				this.$emit('closePop');
			},
			afterRead(file) {
      			// 此时可以自行将文件上传至服务器
      			console.log(file);
    		},
    		changeName(){
    			if(this.remarkName==this.nowChatNickname){
    				this.isChangeColor=false;
    				return;
    			}
    			this.isChangeColor=true;
    		},
    		changeValue(){
    			if(this.label=='' && this.phone=='' && this.desc=='' ){
    				this.isChangeColor=false;
    				return;
    			}
    			this.isChangeColor=true;
    		},


		},
		created(){
			// console.log(1);
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	#setDescLabel{
		margin-bottom: 1.8rem;
	}
	header{
		height: 1rem;
	}
	.btn{
	    height: .6rem;
	    font-size: .36rem;
	    background: #e3e3e3;
	    border-radius: .1rem;
	    color: #b3b3b3;
	    position: absolute;
	    right: .3rem;
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    padding: 0 .24rem;
	}
	.btn.color{
		background: #f53c48;
		color:#fff;
	}
	.main >>> .van-field__control{
		font-size: .4rem;
	}
	.van-cell{
		    padding:.3rem .34rem;
	}
	.main{
		display: flex;
    	flex-direction: column;
    	margin-top: .4rem;
	}
	.main .title{
		font-size: .34rem;
	    color: #878787;
	    display: inline-block;
	    margin: .28rem;
	}
	.main >>> .van-uploader{
		width: 100%;
	}
	.main >>> .van-uploader__wrapper {
		justify-content: center;
	}
	.main >>> .van-uploader__upload {
		width: 80vw;
	    height: 25vh;
	    margin: .2rem;
	    border: .04rem dashed #e5e5e5;
	}
	.main >>> .van-uploader__upload-text {
		font-size: .38rem;
	}
	.main .paddingBottom{
		padding-bottom: .4rem;
	}
	.main >>>.van-uploader__upload-icon{
		display: none;
	}
</style>


