<template>
	<div id="friendPermissions">
		<similarHeader title='朋友权限' @goBack='goBack' :haveMore='false'></similarHeader>
		<div class="main">
			<p class='title'>朋友圈和视频动态</p>
			<ul>
				<li>
					不让{{sex=="男"?"他":"她"}}看我
					<van-switch v-model="checked1" />
				</li>
				<li>
					不看{{sex=="男"?"他":"她"}}
					<van-switch v-model="checked2"  />
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		name:'friendPermissions',
		props:['sex'],
		data(){
			return{
				checked1:false,
				checked2:false
			}
		},
		methods:{
			goBack(){
				this.$emit('closePop');
			},
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	.title{
		font-size: .36rem;
		color:#878787;
		margin:.26rem ;
		margin-top: .5rem;
	}
	.main>ul{
		background: white;
	}
	.main>ul>li{
		padding: .3rem 0;
		margin:0 .3rem;
		font-size: .38rem;
		display: flex;
	    align-items: center;
	    justify-content: space-between;
	}
	.main>ul>li:first-child{
		    border-bottom: 1px solid rgba(237,237,237,.5);
	}
	.main .van-switch{
		background: #989898;
		font-size: .52rem !important;
	}
	.main .van-switch--on {
		background: #05c160 ;
	}
</style>


