<template>
	<div id="moreInfo">
		<similarHeader title='更多' @goBack='goBack' :haveMore='false'></similarHeader>
		<ul>
			<li>
				<span>我和{{sex=="男"?"他":"她"}}的共同群聊</span>
				<span class='count'>{{Math.floor(Math.random()*count)}}个 
					<img src="~img/images/right.png" alt="">
				</span>
				
			</li>
			<li>
				<span>个性签名</span>	
				<span>Personalized signature</span>
			</li>
			<li>
				<span>来源</span>
				<span>通过群聊添加</span>
			</li>
		</ul>
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		name:'moreInfo',
		props:['sex'],
		data(){
			return{
				count:7
			}
		},
		methods:{
			goBack(){
				this.$emit('closePop');
			},
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	ul{
		margin-top: .5rem;
	}
	ul li{
		font-size: .38rem;
		padding: .3rem;
    	background: white;
    	display: flex;
	    align-items: center;
	    justify-content: space-between;
	}
	ul li:first-child{
		border-bottom: .2rem solid #ededed;
	}
	ul li:nth-of-type(2){
		border-bottom: 1px solid #ededed;
	}
	ul li img{
		display:inline-block;
		width:.41rem;height: .41rem;
		margin-left: .2rem;
	}
	ul li .count{
		display: flex;
    	align-items: center;
	}
	ul li span:last-child{
		color:#878787;
	}
</style>


